*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: $fz-default;
  line-height: 24px;
  font-family: $ff-placeholder;
  color: $color-default-black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  font-family: "OpenSans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  width: 100%;
  height: 100%;
  color: $color-default-black;
  background-color: $color-default-white;
}

a {
  display: block;
  color: $color-default-black;
  text-decoration: none;
  cursor: pointer;
  transition: $trans-default;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}
